.header {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: row;
    background: #fff;
    padding: 20px 30px;
    align-items: center;
    box-shadow: 1px 1px 2px rgba(0,0,0,.12);
    &__user-wrp {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        align-items: center;
        margin-left: auto;
        i {
            cursor: pointer;
            margin: 0 13px;
            margin-right: 30px; }
        svg {
            margin: 0 13px;
            cursor: pointer;
            fill: #A5A5A5; }
        img {
            width: 40px;
            height: 40px;
            border-radius: 50px;
            object-fit: cover;
            cursor: pointer; } }
    &__user-text {
        @include font-family($mont-semibld);
        font-size: 14px;
        margin-right: 16px; }
    &__user-descript {
        @include font-family($open-reg);
        color: #8C8C8C; }
    &__name-product {
        max-width: 270px;
        color: #8C8C8C;
        margin-left: 20px;
        text-overflow: ellipsis;
        overflow: hidden;
        height: 30px; }
    &__save.btn {
        width: 124px;
        margin: 0 20px; }
    &__search {
        margin-left: 30px;
        margin-right: 12px;
        flex-grow: 1;
        position: relative;
        label {
            padding: 10px;
            padding-left: 20px;
            padding-right: 40px;
            border: 2px solid #1380F6;
            border-radius: 100px;
            display: block;
            width: 100%;
            svg {
                position: absolute;
                right: 14px;
                top: 14px;
                width: 18px;
                height: 18px;
                fill: #1380F6;
                cursor: pointer; } }
        input {
            @include font-family($mont-semibld);
            font-size: 14px;
            width: 100%; }
        button {
            display: block;
            position: absolute;
            right: 0;
            padding: 8px;
            background: none;
            cursor: pointer;
            @include font-family($open-reg);
            font-size: 13px;
            svg {
                width: 9px;
                height: 6px; } } }
    .search-simple {
        display: none;
        svg {
            transform: rotate(180deg); } }
    &__search--more {
        button {
            display: none; }
        .search-simple {
            display: block; } }
    &__add-product {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        align-items: center;
        flex-grow: 1;
        i {
            width: 32px;
            height: 32px;
            padding: 7px;
            border-radius: 100px;
            text-align: center;
            display: inline-block;
            cursor: pointer;
            position: relative;
            font-style: normal;
            &:hover {
                background: #156ECE;
                span {
                    display: block; } }
            span {
                display: none;
                position: absolute;
                background: rgba(0,0,0,0.5);
                border: none;
                @include font-family($open-reg);
                font-size: 13px;
                padding: 5px 13px;
                border-radius: 50px;
                bottom: -40px;
                left: 50%;
                transform: translateX(-50%); } }
        svg {
            fill: #fff;
            width: 100%; } }
    &__add-wrp {
        background: #1380F6;
        border-radius: 100px;
        padding: 9px 12px;
        margin-left: auto;
        span {
            @include font-family($mont-semibld);
            color: #fff;
            vertical-align: text-bottom;
            padding-right: 20px;
            padding-left: 20px;
            border-right: 1px solid #156ECE;
            margin-right: 10px; } }
    &__csv-wrp {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        button {
            padding: 5px;
            @include font-family($open-reg);
            font-size: 13px;
            color: #8C8C8C;
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            align-items: center;
            margin: 0 20px;
            background: none;
            svg {
                width: 24px;
                height: 24px;
                fill: #A5A5A5;
                margin-right: 10px; } } }
    .search__select-wrp {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        width: 350px;
        margin-left: auto;
        p {
            margin-right: 20px; }
        .select2,
        select {
            width: 100% !important; } }

    .search__select-wrp + .header__user-wrp {
        margin-left: 10px; } }



.title-line {
    position: relative;
    @include font-family($mont-semibld);
    font-size: 13px;
    margin-top: 30px;
    margin-bottom: 15px;
    width: 100%;

    &:before {
        position: absolute;
        content: '';
        width: 100%;
        border-bottom: 1px solid #E9ECEF;
        top: 50%;
        z-index: 1; }

    span {
        background: #fff;
        z-index: 2;
        position: relative;
        padding-right: 20px;
        color: #A3A6A9; } }
.footer {
    a {
        @include font-family($open-reg);
        text-decoration: none;
        color: #848586; } }

