.checkbox[type=checkbox] {
  display: none;
}

.checkbox + .input-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  background-color: #FFF;
  border: 2px solid #ADB5BD;
  margin-right: 10px;
  width: 20px;
  height: 20px;
