  content: '\f00c';
  position: absolute;
  display: inline-block;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 10px;
  line-height: 1;
  left: 3px;
  top: 4px;
}

.label-checkbox--disabled {
  color: #737373;
