.login__social-icons i {
  display: inline-block;
}

.login__forget {
  font-size: 13px;
  text-align: right;
}

.login__forget a {
  color: #156ECE;
}

.login__footer {
  margin-top: auto;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-family: "montserrat-semibold", Arial, sans-serif;
  font-size: 14px;
}

.login__footer p, .login__footer a {
  font-family: "montserrat-semibold", Arial, sans-serif;
  color: #000;
  text-decoration: none;
}

.login__footer .login__reg-link {
  color: #1380F6;
  text-decoration: underline;
}

.login .icon-russia {
  display: inline-block;
  vertical-align: text-bottom;
  margin-right: 20px;
}

.login .icon-lock {
  fill: #fff;
  height: 18px;
  width: 14px;
}

.login .span-input-text {
  font-family: "montserrat-medium", Arial, sans-serif;
  font-size: 16px;
}

.login .input-text:not(:placeholder-shown) + .span-input-text {
  font-family: "open-sans-regular", Arial, sans-serif;
}

.login .label-checkbox {
  margin-top: 10px;
}

.login .label-checkbox a {
  color: #000;
}

.login__steps {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 78px;
  width: 268px;
}

.login__steps:before {
  content: '';
  position: absolute;
  border: 2px solid #DEE2E6;
  width: 100%;
  z-index: 1;
  left: 0;
  right: 0;
  top: 17px;
}

.login__step {
  width: 36px;
  height: 36px;
  border: 2px solid #dee2e6;
  border-radius: 50px;
  text-decoration: none;
  font-family: "montserrat-semibold", Arial, sans-serif;
  font-size: 18px;
  text-align: center;
  line-height: 2;
  box-shadow: 0px 0px 0px 14px #fff;
  position: relative;
  z-index: 2;
  background: #fff;
}

.login__step:nth-child(2) {
  margin: 0 80px;
}

.login__step svg {
  width: 18px;
}

.login__step--check {
  line-height: 2.2;
  border: 2px solid #1380F6;
}

.login__step--check svg {
  fill: #1380F6;
}

.login__step--current {
  border: 2px solid #1380F6;
  background: #1380F6;
  color: #fff;
}

.login__step span {
  position: absolute;
  content: '';
  top: 42px;
  left: 50%;
  transform: translateX(-50%);
  font-family: "open-sans-regular", Arial, sans-serif;
  font-size: 13px;
  line-height: 20px;
  color: #8B8B8B;
}

.label-text--password input {
  padding-right: 25px;
}

.label-text--password .login__btn-eye {
  position: absolute;
  content: '';
  top: 32px;
  right: 13px;
  width: 20px;
  height: 20px;
  padding: 2px;
  cursor: pointer;
}

.label-text--password svg {
  width: 16px;
  fill: #BFBFBF;
}

.label-text--password .icon-eye--active {
  fill: #1380F6;
}

.out-registration .login__right,
.out-reset-password .login__right {
  background-position: right;
}

.out-reset-password .login__block {
  padding-top: 130px;
}

.out-reset-password .login__block .btn {
  width: 300px;
  margin: 31px auto 0;
}

.btn--center {
  justify-content: center;
}

.out-registration .login__block {
  padding-top: 67px;
}

.out-registration .login__block form {
  padding-bottom: 24px;
}

.login__footer--center {
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  .main-login {
    min-height: auto;
  }
}
